import { useCallback, useEffect, useState } from "react";
import { useTranslation } from "react-i18next";
import { useLocation } from "react-router-dom";
import { assestsNavbarUrl } from "../../../../Services/UrlConfig/AssestsBaseUrl";
import style from "../../../FinanceManager/FinanceManager.module.css";
import BorderBottom from "../../../../Ui/Border/BorderBottom";
import ApplicationSubjectListDetailCard from "./ApplicationSubjectListDetailCard";
import UserMemberContainerWrapper from "../../../../Ui/UserMember/UserMemberContainerWrapper";
import UserMemberContentWrapper from "../../../../Ui/UserMember/UserMemberContentWrapper";
import { useInView } from "react-intersection-observer";
import { useOneApplicationOneSubjectStudentList } from "../../../../Hooks/ApiHooks/AdmissionManager/admission-manager-api";
import { nextPage } from "../../../../Utils/Functions/nextPage";
import { customDebounce } from "../../../../Utils/Functions/debounce";
import QvipleLoader from "../../../../Loader/QvipleLoader";
import Loading from "../../../../Loader/Loading";
import EmptyMessage from "../../../../Utils/EmptyMessage/EmptyMessage";
const ApplicationSubjectListDetail = () => {
  const { t } = useTranslation();
  const getQuery = useLocation();
  const [search, setSearch] = useState("");
  const [page, setPage] = useState(1);
  const [state, setState] = useState(true);
  const [ref, inView] = useInView({ skip: search });

  const {
    oneApplicationOneSubjectStudentList,
    oneApplicationOneSubjectStudentListLoading,
  } = useOneApplicationOneSubjectStudentList({
    data: {
      smid: getQuery.state?.smid,
      aid: getQuery.state?.appId,
      page: page,
      limit: 10,
      search: search,
    },
    skip: !getQuery.state?.appId
      ? getQuery.state?.smid
      : !getQuery.state?.appId,
  });
  useEffect(() => {
    if (inView && state) setPage((page) => page + 1);
  }, [inView, state]);
  useEffect(() => {
    if (oneApplicationOneSubjectStudentList?.list?.length) {
      let obj = nextPage(oneApplicationOneSubjectStudentList?.list?.length);
      setState(() => obj);
    }
  }, [oneApplicationOneSubjectStudentList?.list?.length]);

  const onDebounce = useCallback(
    customDebounce((val) => {
      setPage(() => 1);
      setSearch(val);
    }, 1500),
    []
  );

  const onSearchEvent = (e) => {
    if (e.target.value) onDebounce(e.target.value);
    else {
      setPage(() => 1);
      setSearch("");
    }
  };

  return (
    <>
      <UserMemberContainerWrapper>
        <UserMemberContentWrapper>
          <div className={style.fm_header_container}>
            <h6>
              {getQuery.state?.subjectName ?? ""}
              {` `}({t("student_list")})
            </h6>
            <div className={style.fm_search}>
              <section className={style.fm_search_container}>
                <div className={style.fm_search_container_inner}>
                  <img
                    className={style.fm_search_icon}
                    alt="search icon"
                    src={`${assestsNavbarUrl}/navbar-search.svg`}
                  />
                  <input
                    type="text"
                    placeholder={t("search")}
                    onChange={onSearchEvent}
                  />
                </div>
              </section>
            </div>
          </div>
        </UserMemberContentWrapper>
        <BorderBottom />
        <UserMemberContentWrapper
          customStyle={{
            paddingTop: "0",
          }}
        >
          {oneApplicationOneSubjectStudentList?.list?.map((student, index) =>
            oneApplicationOneSubjectStudentList?.list?.length === index + 1 ? (
              <div key={student?._id} ref={ref}>
                <ApplicationSubjectListDetailCard student={student} />
              </div>
            ) : (
              <ApplicationSubjectListDetailCard
                student={student}
                key={student?._id}
              />
            )
          )}
          {oneApplicationOneSubjectStudentListLoading ? (
            search ? (
              <QvipleLoader />
            ) : (
              <Loading />
            )
          ) : search ? (
            oneApplicationOneSubjectStudentList?.list?.length === 0 && (
              <EmptyMessage
                customStyleContainer={{
                  marginTop: "1.5rem",
                }}
                title="No students found related this search."
              />
            )
          ) : (
            oneApplicationOneSubjectStudentList?.list?.length === 0 && (
              <EmptyMessage
                customStyleContainer={{
                  marginTop: "1.5rem",
                }}
                title="No students."
              />
            )
          )}
        </UserMemberContentWrapper>
      </UserMemberContainerWrapper>
    </>
  );
};

export default ApplicationSubjectListDetail;
